$primary: #222222 !default; $secondary: #ffffff !default; $tertiary: #0088cc !default; $quaternary: #e45735 !default; $header_background: #ffffff !default; $header_primary: #333333 !default; $highlight: #ffff4d !default; $selected: #e9e9e9 !default; $hover: #f2f2f2 !default; $danger: #c80001 !default; $success: #009900 !default; $love: #fa6c8d !default;  $public_image_path: "http://127.0.0.1:4200/images";  @import "common/foundation/variables"; @import "common/foundation/mixins"; .discourse-local-date {
  > * {
    pointer-events: none;
  }

  &.cooked-date {
    color: var(--primary);
    cursor: pointer;
    border-bottom: 1px dashed var(--primary-medium);
    white-space: nowrap;

    .d-icon {
      color: var(--primary);
    }

    &.past {
      border-bottom-color: var(--primary-low-mid);
    }

    &.past[data-countdown] {
      color: var(--primary-medium);
    }
  }
}

.locale-dates-previews {
  max-width: 250px;

  .preview {
    display: flex;
    flex-direction: column;
    padding: 5px;
    margin: 0;

    .timezone {
      font-weight: 700;
    }

    &.current {
      background: var(--tertiary-low);
    }
  }
}

.download-calendar {
  text-align: right;
  cursor: pointer;
  margin-top: 0.5em;
}
